/* eslint-disable prefer-const */
import { reactive, ref } from 'vue';

export default function useInputMask(clearBirthday, clearPassportIssuedDate) {
	const birthdayMask = 'RD.ZY.QWXX';
	const birthdayTokens = reactive({
		R: {
			pattern: /[0-3]/,
		},
		D: {
			pattern: /[0-9]/,
			transform: (char) => {
				if (clearBirthday.value[0] === '3' && !clearBirthday.value[1].match(/[0-1]/)) {
					return null;
				} if (clearBirthday.value[0] === '0' && !clearBirthday.value[1].match(/[1-9]/)) {
					return null;
				}

				return char;
			},
		},
		X: {
			pattern: /[0-9]/,
		},
		Z: {
			pattern: /[0-1]/,
		},
		Y: {
			pattern: /[0-9]/,
			transform: (char) => {
				if (clearBirthday.value[2] === '1' && !clearBirthday.value[3].match(/[0-2]/)) {
					return null;
				}
				if (clearBirthday.value[2] === '0' && clearBirthday.value[3].match(/0/)) {
					return null;
				}

				return char;
			},
		},
		Q: {
			pattern: /[1-2]/,
		},
		W: {
			pattern: /[0,9]/,
			transform: (char) => {
				if (clearBirthday.value[4] === '2' && !clearBirthday.value[5].match(/0/)) {
					return null;
				}
				if (clearBirthday.value[4] === '1' && clearBirthday.value[5].match(/0/)) {
					return null;
				}

				return char;
			},
		},
	});

	const issuedDateMask = 'RD.ZY.QWXX';
	const issuedDateTokens = reactive({
		R: {
			pattern: /[0-3]/,
		},
		D: {
			pattern: /[0-9]/,
			transform: (char) => {
				if (clearPassportIssuedDate.value[0] === '3' && !clearPassportIssuedDate.value[1].match(/[0-1]/)) {
					return null;
				} if (clearPassportIssuedDate.value[0] === '0' && !clearPassportIssuedDate.value[1].match(/[1-9]/)) {
					return null;
				}

				return char;
			},
		},
		X: {
			pattern: /[0-9]/,
		},
		Z: {
			pattern: /[0-1]/,
		},
		Y: {
			pattern: /[0-9]/,
			transform: (char) => {
				if (clearPassportIssuedDate.value[2] === '1' && !clearPassportIssuedDate.value[3].match(/[0-2]/)) {
					return null;
				}
				if (clearPassportIssuedDate.value[2] === '0' && clearPassportIssuedDate.value[3].match(/0/)) {
					return null;
				}

				return char;
			},
		},
		Q: {
			pattern: /[1-2]/,
		},
		W: {
			pattern: /[0,9]/,
			transform: (char) => {
				if (clearPassportIssuedDate.value[4] === '2' && !clearPassportIssuedDate.value[5].match(/0/)) {
					return null;
				}
				if (clearPassportIssuedDate.value[4] === '1' && clearPassportIssuedDate.value[5].match(/0/)) {
					return null;
				}

				return char;
			},
		},
	});

	const passportCodeMask = 'XXX-XXX';
	const passportCodeTokens = reactive({
		X: {
			pattern: /[0-9]/,
		},
	});

	const phoneMask = '+7 (f##) ###-##-##';
	const phoneTokens = reactive({
		f: {
			pattern: /[9]/,
		},
		'#': {
			pattern: /[0-9]/,
		},
	});

	const codeMask = 'XXXX';
	const codeTokens = reactive({
		X: {
			pattern: /[0-9]/,
		},
	});

	let cardMask = ref('#### #### #### ####');
	const cardTokens = reactive({
		'#': {
			pattern: /[0-9]/,
		},
	});

	const cardDateMask = '$#/##';
	const cardDateTokens = reactive({
		$: {
			pattern: /[0-1]/,
		},
		'#': {
			pattern: /[0-9]/,
		},
	});

	return {
		birthdayTokens,
		birthdayMask,

		phoneTokens,
		phoneMask,

		codeTokens,
		codeMask,

		issuedDateTokens,
		issuedDateMask,

		passportCodeTokens,
		passportCodeMask,

		cardMask,
		cardTokens,

		cardDateMask,
		cardDateTokens,
	};
}
