export const secondsToTime = (e) => {
	const h = Math.floor(e / 3600).toString().padStart(2, '0');
	// eslint-disable-next-line no-mixed-operators
	const m = Math.floor(e % 3600 / 60).toString().padStart(2, '0');
	const s = Math.floor(e % 60).toString().padStart(2, '0');

	const result = `${h}:${m}:${s}`;

	if (result.split(':').length === 3 && result.split(':')[0].includes('00')) {
		return `${m}:${s}`;
	}

	return `${h}:${m}:${s}`;
}

const base64toBlob = ({base64, contentType = '', sliceSize = 512}) => {
	let byteCharacters = atob(base64);
	let byteArrays = [];

	for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
		let slice = byteCharacters.slice(offset, offset + sliceSize);

		let byteNumbers = new Array(slice.length);
		for (let i = 0; i < slice.length; i++) {
			byteNumbers[i] = slice.charCodeAt(i);
		}

		let byteArray = new Uint8Array(byteNumbers);
		byteArrays.push(byteArray);
	}

	let blob = new Blob(byteArrays, {type: contentType});
	return blob;
}

const blobToLink = (blob) => {
	return window.URL.createObjectURL(blob);
}

export const base64toBlobLink = ({base64, contentType = '', sliceSize = 512}) => {
	const blob = base64toBlob({base64, contentType, sliceSize});
	const link = blobToLink(blob);

	return link;
}

export const setClearValue = (data) => {
	// return birthday.replace(/\./g, '').replace(/-/g, '');
	return data.replace(/\D/g, '');
}

export const setClearPhone = (phone) => {
    let replacePhone = String(phone).replace(/\D+/g, "");

    let cleanPhoneNumber = replacePhone;
        if (replacePhone[0] == '7' || replacePhone[0] == '8') {
            cleanPhoneNumber = replacePhone.slice(1, 11);
        }

    return cleanPhoneNumber;
}

export const formatBirthday = (birthday) => {
    let date = String(birthday).replace(/\D+/g, "");
    let day = date.slice(0, 2);
    let month = date.slice(2, 4);
    let year = date.slice(4, 8);

    return `${year}-${month}-${day}`;
}

export const formatPhone = (v) => {
    if (v && v.length == 10) {
        return `+7 (${v[0]}${v[1]}${v[2]}) ${v[3]}${v[4]}${v[5]}-${v[6]}${v[7]}-${v[8]}${v[9]}`;
    } else {
        return v;
    }
}

export const dateTimeFilter = (value) => {
    let formateDate = new Date(value);
    let nowDay = formateDate.getDate(),
        nowMonth = formateDate.getMonth() + 1,
        nowYear = formateDate.getFullYear(),
        nowHour = formateDate.getHours(),
        nowMinute = formateDate.getMinutes(),
        nowSecond = formateDate.getSeconds();

    if (nowDay < 10) {
        nowDay = "0" + nowDay;
    }

    if (nowMonth < 10) {
        nowMonth = "0" + nowMonth;
    }

    if (nowHour < 10) nowHour = "0" + nowHour;
    if (nowMinute < 10) nowMinute = "0" + nowMinute;
    if (nowSecond < 10) nowSecond = "0" + nowSecond;

    return nowDay + "." + nowMonth + "." + nowYear + " " + nowHour + ":" + nowMinute + ":" + nowSecond;
}

// ЗАМЕНА ТОЧКИ НА ЗАПЯТУЮ
export const toCurrency = (value) => {
    let formatter;
    if (typeof value !== "number") {
        formatter = value.replace('.', ',')
        // return value;
    }
    formatter = value.toString().replace('.', ',')
    return formatter
}

