// СОЗДАНИЕ ПРОБЕЛА МЕЖДУ НОМЕРОМ ДОГОВОРА
export const formatDogovor = (value) => {
    return value.toString().replace(/(\d{1,3})(?=((\d{3})*)$)/g, " $1")
}

export const dateFilter = (value, arr = false, split = '.', timeClear = false, reverse = false) => {
    if (String(value).includes('0000-00-00')) {
        return value;
    }

    // Поддержка для IOS
    let dateIOS = value;
    if (!String(value).includes('T')) {
        dateIOS = String(value).replace(' ', 'T');
    }

    let formateDate = new Date(dateIOS);

    if (arr) {
        if (timeClear) {
            value = value.split(' ');
            value = value[0]
        }
        value = value.split(split);
        if (reverse) {
            value = value.reverse()
        }
        formateDate = new Date(value[2], (value[1] - 1), value[0]);
    }

    let nowDay = formateDate.getDate(),
        nowMonth = formateDate.getMonth() + 1,
        nowYear = formateDate.getFullYear();

    if (nowDay < 10) {
        nowDay = "0" + nowDay;
    }

    if (nowMonth < 10) {
        nowMonth = "0" + nowMonth;
    }

    return nowDay + "." + nowMonth + "." + nowYear;
}

export const formatDate = (value) => {
    let arr_value = value.split('.');
    let formateDate = new Date(arr_value[2], arr_value[1], arr_value[0]);
    let nowDay = formateDate.getDate(),
        nowMonth = formateDate.getMonth() + 1,
        nowYear = formateDate.getFullYear();

    if (nowDay < 10) {
        nowDay = "0" + nowDay;
    }

    if (nowMonth < 10) {
        nowMonth = "0" + nowMonth;
    }

    return nowDay + "." + nowMonth + "." + nowYear;
}

// SPRINTF ИЗ PHP В JS - ЗАМЕНА В ТЕКСТЕ ПЕРЕМЕННЫХ
export const replaceText = (text, props) => {
    if (props) {
        props.forEach((item, i) => {
            let indx = String(i + 1);
            text = text.replace('{' + indx + '}', item)
        });
    }

    return text;
}
