<template>
	<div class="my-form-control-wrap" :class="extraClass">
		<div
			class="my-form-control"
			:class="[
				{
					'focused': isFocused,
					'empty': isEmpty,
					'invalid': error.value,
					'valid': valid && !isEmpty,
					'disabled': disabled
				},
				extraClass
			]">
			<label :class="extraClass" class="my-form-control__label" :for="labelFor">{{ label }}</label>
			<input
				ref="myformcontrol"
				v-maska="setMask"
				:id="labelFor"
				class="my-form-control__input"
				:class="[{'focused': isFocused}, extraClass]"
				:type="type"
				@input="$emit('update:modelValue', $event.target.value)"
				:value="modelValue"
				@focus="onFocus"
				@blur="onBlur"
				:disabled="disabled"
				:autocomplete="autocomplete"
			>
		</div>
		<div v-if="error.text && !extraClass.includes('cardDate')" class="my-form-control-error">{{ error.text }}</div>
	</div>
</template>

<script setup>
import {
	defineProps, defineEmits, ref, watchEffect, computed, watch,
} from 'vue';

const myformcontrol = ref(null);

const props = defineProps({
	modelValue: [String, Number],
	autocomplete: {
		type: String,
	},
	label: {
		type: String,
		required: true,
	},
	labelFor: {
		type: String,
		required: true,
	},
	type: {
		type: String,
		default: 'text',
	},
	mask: {
		type: String,
		default: '',
	},
	maskTokens: {
		type: Object,
	},
	focusOnInit: {
		type: Boolean,
		default: false,
	},
	error: {
		type: [Boolean, String, Object],
	},
	valid: {
		type: Boolean,
		default: false,
	},
	disabled: {
		type: Boolean,
		default: false,
	},
	extraClass: {
		type: String,
		default: '',
	},
	controlType: {
		type: String,
	},
	options: {
		type: Array,
		default: () => [],
	},
	customFocus: {
		type: Boolean,
		default: false,
	},
});

defineEmits(['update:modelValue', 'update:addressKey', 'update:cityId', 'update:bankId']);

const isFocused = ref(false);
const isEmpty = ref(true);

const onFocus = () => {
	isFocused.value = true;
};
const onBlur = () => {
	isFocused.value = false;
};

const setMask = computed(() => {
	if (props.mask && props.maskTokens) {
		return {
			mask: props.mask,
			tokens: props.maskTokens,
		};
	}

	return null;
});

watchEffect(() => {
	// console.log('props.modelValue', props.modelValue);
	if (props.modelValue?.length > 0) {
		isEmpty.value = false;
	} else {
		isEmpty.value = true;
	}
});

watch(
	[() => props.controlType, () => props.customFocus],
	(val) => {
		const [type, focus] = val;

		console.log('FOCUS PHONE: ', type, focus);
		if ((type === 'login-code' || type === 'loan-docs-phone') && focus) {
			console.log('FOCUS');
			myformcontrol.value.focus();
		}
	},
);

</script>
<style lang="scss" scoped>
@import '../assets/styles/input.scss';
</style>
