<template>
	<div class="auth-by-esia">
		<Button class="button button--auth-by-esia" :handler="esiaLogin" :isPreloader="true">
			<img v-if="!mobile" src="./img/gosuslugi.png" alt="госуслуги"/>
			<img v-else src="./img/gosuslugi-sm.png" alt="госуслуги"/>

			<span v-if="!mobile" class="auth-by-esia__text">Авторизоваться через Госуслуги <br> и получить скидку</span>
			<span v-else class="auth-by-esia__text">Авторизоваться через <br> Госуслуги и получить скидку</span>
		</Button>
		<p class="auth-by-esia__secure">Безопасно и полностью онлайн. Ваши данные защищены.</p>
	</div>
</template>

<script setup>
import Button from '@/shared/components/Button';
import { computed } from 'vue';
import { useStore } from 'vuex';
import { postApi } from '@/shared/api';

const store = useStore();
const mobile = computed(() => store.getters['isMobile/mobile']);

const esiaLogin = async () => {
	try {
		// const response = await getApi('/api/digital-profile/login');
		const response = await postApi('/api-k3/v2/esia/login');
		console.log('response esia: ', response);
		if (response.data.status === 'ok') {
			if (response.data.data?.esia_url) {
				console.log('SUCCESS');
				window.location.replace(response.data.data.esia_url);
				// store.commit('SET_GLOBAL_PRELOADER', false);
			}
		} else if (response.data.status === 'error') {
			console.log('error: ', response.data);
		}
	} catch (error) {
		console.log('esiaLogin error: ', error);
	}
};

</script>
<style lang="scss">
@import './auth-by-esia';

</style>
