		// store.commit('SET_BUTTON_PRELOADER', true);

		const response = await store.dispatch('authByPhone/sendCode', {
			phone: clearPhone.value,
			birthday: formatBirthday(clearBirthday.value),
			sms_code: codeValue.value,
		});

