			console.log('Токен получен:', response);

			store.commit('SET_TOKEN', response.data.data.token);
			setCookie('token', response.data.data.token);
			store.commit('SET_CENTRIFUGE_TOKEN', response.data.data.centrifugal.token);
			setCookie('centrifugeToken', response.data.data.centrifugal.token);

