	fields.code.error.text = '';
	fields.code.error.value = false;
	fields.birthday.error.text = '';
	fields.birthday.error.value = false;
}

const changePhoneNumber = () => {
	authStep.value = 1;
	oldPhone.value = fields.phone.value;
	resetFields();
};
</script>
